<template>
  <va-card :title="$t('tables.labelEmployees')" class="w-100">
    <va-button
      icon="fa fa-plus"
      flat
      slot="actions"
      class="mr-0"
      @click="showCreateModal"
    />
    <va-data-table
      :fields="fields"
      :data="dataTable"
      :loading="loading"
      :totalPages="totalPages"
      @page-selected="readItems"
      api-mode
      hoverable
    >
      <template slot="marker" slot-scope="props">
        {{props.rowIndex + 1}}
      </template>
      <template slot="is_Delivery" slot-scope="props">
        {{props.rowData.is_Delivery  ? '&#10004;': '&#10008;' }}
      </template>
      <template slot="work_Date" slot-scope="props">
        {{ formatDate(props.rowData.work_Date)}}
      </template>
      <template slot="branchs" slot-scope="props">
        {{props.rowData.branchs === null ? '-----': props.rowData.branchs.name }}
      </template>
      <template slot="actions" slot-scope="props">
        <va-button flat small color="gray" @click="showEditModal(props.rowData)" class="ma-0">
          {{ $t('tables.edit') }}
        </va-button>

        <va-button flat small color="danger" @click="showDeleteModal(props.rowData)" class="ma-0">
          {{ $t('tables.delete') }}
        </va-button>
      </template>
    </va-data-table>

    <va-modal v-model="deleteModal" no-outside-dismiss
      :title=" $t('generic.deleteItemTitle') "
      :okText=" $t('modal.delete') "
      :cancelText=" $t('modal.cancel') "
      :message=" $t('generic.deleteItemMsg') "
      @ok="okDeletefun()"
      @cancel="cancleDeletefun()">
    </va-modal>

    <va-modal v-model="createModal" no-outside-dismiss
      size="large"
      :title="$t(modelTitle)"
      hide-default-actions >
      <form @submit.prevent="editModal? okEditfun()  :okCreatefun()">
        <div class="row flex-center">
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.name')}} </label>
            <va-input v-model="currentobject.name"
              :error="riquierdvalidation(currentobject.name)"
              :error-messages="$t('errorMessage.required')"
              :placeholder="$t('placeholder.name')" required/>
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.email')}} </label>
            <va-input v-model="currentobject.email"
              :error="validateEmail(currentobject.email)"
              :error-messages="$t('errorMessage.email')"
              :placeholder="$t('placeholder.email')" />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <va-input v-model="currentobject.mobile"
              :error="validatePhone(currentobject.mobile)"
              :error-messages="$t('errorMessage.phone')"
              :placeholder="$t('placeholder.phone')" required/>
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.ssn')}} </label>
            <va-input v-model="currentobject.ssn"
              :placeholder="$t('placeholder.ssn')" />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.salary_basic')}} </label>
            <va-input v-model="currentobject.salary_basic" type="number" min="0"
              :placeholder="$t('placeholder.salary_basic')" />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.nationality')}} </label>
            <va-input v-model="currentobject.nationality"
              :placeholder="$t('placeholder.nationality')" />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.address')}} </label>
            <va-input v-model="currentobject.address"
              :placeholder="$t('placeholder.address')" />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.branchs')}} </label>
            <va-select
              :label="$t('placeholder.branchs')"
              v-model="fK_Branchs.value"
              required
              searchable
              textBy="name"
              keyBy="id"
              :options="fK_Branchs.options"
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <va-checkbox
              :label="$t('placeholder.is_Delivery')"
              v-model="currentobject.is_Delivery"
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <va-checkbox
              :label="$t('placeholder.iS_Male')"
              v-model="currentobject.iS_Male"
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.birth_Date')}} </label>
            <va-date-picker
              :label="$t('placeholder.birth_Date')"
              v-model="currentobject.birth_Date"
              required
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.work_Date')}} </label>
            <va-date-picker
              :label="$t('placeholder.work_Date')"
              v-model="currentobject.work_Date"
              required
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.notes')}} </label>
            <va-input v-model="currentobject.notes"
              :placeholder="$t('placeholder.notes')" />
          </div>

        </div>
        <div class="d-flex justify--center mt-3">
          <va-button type="button" color="gray" flat @click="editModal? cancleEditfun() : cancleCreatefun()">
            {{ $t('modal.cancel') }}
          </va-button>
          <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t(modelOktext) }}</va-button>
        </div>
      </form>

    </va-modal>
  </va-card>

</template>

<script>
import EmployeesService from '../../../../services/BasicData/employeesService'
import BranchsService from '../../../../services/BasicData/BranchsService'

import helper from '../../../../helper/helper'
export default {
  data () {
    return {
      modelTitle: '',
      modelOktext: '',
      stopBtn: false,
      perPage: 10,
      totalPages: 0,
      dataTable: [],
      deleteModal: false,
      editModal: false,
      createModal: false,
      loading: true,
      opjectBeforeEdit: {},
      currentobject: {
        nationality: '',
        birth_Date: '',
        work_Date: '',
      },
      fK_Branchs: {
        value: '',
        options: [],
      },
    }
  },

  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',

      }, {
        name: 'name',
        title: this.$t('tables.headings.name'),
      },
      {
        name: 'mobile',
        title: this.$t('tables.headings.phone'),
      },
      {
        name: 'ssn',
        title: this.$t('tables.headings.ssn'),
      },
      {
        name: 'salary_basic',
        title: this.$t('tables.headings.salary_basic'),
      }, {
        name: '__slot:work_Date',
        title: this.$t('tables.headings.work_Date'),
      }, {
        name: 'notes',
        title: this.$t('tables.headings.notes'),
      }, {
        name: '__slot:branchs',
        title: this.$t('tables.headings.branchs'),
      },
      {
        name: '__slot:is_Delivery',
        title: this.$t('tables.headings.is_Delivery'),
      }, {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },
  },
  created () {
    this.readItems()
    this.getBranchs()
  },
  methods: {
    riquierdvalidation (data) {
      return helper.riquierdvalidation(data)
    },
    numircvalidation (data) {
      return helper.numircvalidation(data)
    },
    numircriquierdvalidation (d) {
      return helper.numircriquierdvalidation(d)
    },
    validateEmail (d = '') {
      if (d === null || d === undefined || d === '') {
        return false
      }
      return d.length >= 1 ? helper.validateEmail(d) : false
    },
    validatePhone (d = '') {
      if (d === null || d === undefined || d === '') {
        return false
      }
      return d.length >= 1 ? helper.validatePhone(d) : false
    },
    formatDate (dateString, FullDate = true) {
      if (FullDate) {
        const options = { weekday: 'short', year: 'numeric', month: 'numeric', day: 'numeric', hour: 'numeric', minute: 'numeric' }
        return new Date(dateString).toLocaleDateString('en-GB', options)
      } else {
        const options = { year: 'numeric', month: 'numeric', day: 'numeric' }
        return new Date(dateString).toLocaleDateString('en-GB', options)
      }
    },
    /*  api get */
    readItems (page = 1) {
      this.loading = true
      const params = {
        PageSize: this.perPage,
        PageNumber: page,
        Customer_type: 1,
      }
      EmployeesService.getAllPagnation(params)
        .then(response => {
          this.loading = false
          this.dataTable = response.data
          var pagination = JSON.parse(response.headers['x-pagination'])
          this.totalPages = pagination.TotalPages
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    getBranchs () {
      BranchsService.getAll()
        .then(response => {
          this.fK_Branchs.options = response.data
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    /*  end api get */
    /* cud api */
    create () {
      if (this.fK_Branchs.value !== '') {
        this.currentobject.fK_Branchs = this.fK_Branchs.value.id
      }
      EmployeesService.create(this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.readItems()
          this.loading = false
          this.currentobject = {
            nationality: '',
            birth_Date: '',
            work_Date: '',
          }
          this.fK_Branchs.value = ''
          this.createModal = false
          this.stopBtn = false

          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 1500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',
        })
      })
    },
    edit () {
      if (this.fK_Branchs.value !== '') {
        this.currentobject.fK_Branchs = this.fK_Branchs.value.id
      }
      EmployeesService.update(this.currentobject.id, this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.readItems()
          this.currentobject = {
            nationality: '',
            birth_Date: '',
            work_Date: '',
          }
          this.fK_Branchs.value = ''
          this.loading = false
          this.editModal = false
          this.createModal = false
          this.stopBtn = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 1500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',
        })
      })
    },
    remove (id) {
      EmployeesService.delete(id).then(response => {
        if (response.headers['x-status'] === 'true') {
          const idx = this.dataTable.findIndex(u => u.id === id)
          this.dataTable.splice(idx, 1)
          this.loading = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 1500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',
        })
      })
    },
    /* cud api */

    /* Delete model */
    showDeleteModal (cuobject) {
      this.deleteModal = true
      this.currentobject = cuobject
    },
    okDeletefun () {
      this.remove(this.currentobject.id)
    },
    cancleDeletefun () {
      this.loading = false
    },
    /* end Delete model */

    /* edit model */
    showEditModal (cuobject) {
      this.editModal = true
      this.modelTitle = 'generic.editItemTitle'
      this.modelOktext = 'modal.edit'
      this.createModal = true
      this.fK_Branchs.value = cuobject.branchs
      this.opjectBeforeEdit = Object.assign({}, cuobject)
      this.currentobject = cuobject
      this.loading = true
    },
    okEditfun () {
      this.stopBtn = true
      this.edit(this.currentobject)
    },
    cancleEditfun () {
      Object.assign(this.currentobject, this.opjectBeforeEdit)
      this.createModal = false
      this.loading = false
      this.currentobject = {
        nationality: '',
        birth_Date: '',
        work_Date: '',
      }
      this.fK_Branchs.value = ''
      this.editModal = false
      this.stopBtn = false
    },
    /* end edit model */

    /* create model */
    showCreateModal () {
      this.currentobject.nationality = 'مصري'
      this.currentobject.work_Date = helper.todayDate()
      this.currentobject.birth_Date = helper.todayDate()
      this.modelTitle = 'generic.creatItemTitle'
      this.modelOktext = 'modal.create'
      this.createModal = true
      this.loading = true
    },
    okCreatefun () {
      this.stopBtn = true
      this.create()
    },
    cancleCreatefun () {
      this.createModal = false
      this.loading = false
      this.currentobject = {
        nationality: '',
        birth_Date: '',
        work_Date: '',
      }
      this.fK_Branchs.value = ''
      this.stopBtn = false
    },
    /* end create model */
  },
}

</script>

<style lang="scss" >
  i.fa.fa-exclamation {
    color: red !important;
  }

  .va-modal__inner {
    max-width: 100%;
  }

</style>
